.icon {
  position: relative;
  display: block;
  width: 100%;
  padding-bottom: 100%;
  text-decoration: none;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;

  &--menu {
    background-image: url(../images/menu.svg);

    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }

  &--phone {
    background-image: url(../images/phone.svg);
  }

  &--cross {
    background-image: url(../images/cross.svg);
  }

  &__tooltip {
    position: absolute;
    text-align: right;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);
    display: block;
    width: max-content;
    padding-right: 20px;

    font-weight: bold;
    font-size: 12px;

    letter-spacing: 2px;
    opacity: 0;
    pointer-events: none;

    transition: opacity 0.3s;
  }

  @include onDesktop {
    &:hover &__tooltip {
      opacity: 1;
      pointer-events: all;
      color: #fff;
    }
  }
}
